.SearchSelector-search {
  width: 100%;
  overflow: hidden;
}
.SearchSelector-listItem {
  transition: 100ms;
  cursor: pointer;
}
.SearchSelector-listItem:hover {
  background: #f2f2f2;
}
.SearchSelector-org {
  margin: 20px 10px;
}
.SearchSelector-hoveredItem {
  background: #e9e9e9;
}
.SearchSelector-list {
  height: calc(100vh - 240px);  
  overflow: scroll;
}